.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    width: 100%;
}
.header__logo{
    object-fit: contain;
    margin-top: 18px;
    width: 100px;
    margin-left: 20px;
    margin-right: 20px;
    cursor: pointer;
}

.header__search{
    display: flex;
    align-items: center;
    flex:1;
}

.header__searchInput{
    height: 12px;
    padding: 10px;
    width: 100%;
    border: none;
}
.header__searchIcon{

    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}

.header__nav{
    display: flex;
    justify-content: space-evenly;

}
.header__link{
    color: white;
    text-decoration: none;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}
.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket{
    color: white;
    display: flex;
    align-items: center;
}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}